import React from "react";
import Select from "react-select";

/**
 * Componente ChartSelector que muestra un selector de tipos de grfico.
 * @component
 * @param {object} props - Propiedades del componente.
 * @param {Function} props.onChartSelect - Funcin para manejar la seleccin del tipo de grfico.
 * @returns {JSX.Element} Componente ChartSelector.
 */
function ChartSelector({ onChartSelect }) {
  const options = [
    { value: "bar", label: "Grfico de Barras" },
    { value: "line", label: "Grfico de Lneas" },
    { value: "pie", label: "Grfico de Tarta" },
  ];

  /**
   * Maneja el evento de seleccin de opcin del selector.
   * @param {object} selectedOption - Opcin seleccionada.
   */
  const handleChartSelect = (selectedOption) => {
    onChartSelect(selectedOption.value);
  };

  // Configuracin de idioma para las indicaciones
  const selectLanguageConfig = {
    noOptionsMessage: () => "No hay opciones disponibles",
    placeholder: "Selecciona un tipo de grfico",
  };

  return (
    <Select
      options={options}
      onChange={handleChartSelect}
      className="chart-selector"
      {...selectLanguageConfig}
    />
  );
}

export default ChartSelector;
